import React from 'react';
import { Modal, Checkbox, Input } from 'antd';
import { useHistory } from 'react-router-dom';
import { PlusCircleOutlined, MinusCircleOutlined } from '@ant-design/icons';
import { Increment } from '../../../services/database';
import { func, bool, arrayOf, object } from 'prop-types';
import { CheckboxStyle, ObjectDetail, ObjectName, ObjectWrapper, ObjectTotal, InputTotalObject, ObjectLine } from './style';

const ConfirmModal = ({ visible, setVisible, data }) => {
  const { replace } = useHistory();

  const handleOk = () => {
    setVisible(false);
    data.forEach(object => {
      Increment('/object/' + object.name, object.total);
    });
    Modal.success({
      title: 'Berhasil',
      content: 'Data berhasil disimpan!',
      onOk: () => replace('/'),
    })
  };

  const handleCancel = () => {
    setVisible(false);
  };

  return (
    <Modal title="Konfirmasi" visible={visible} onOk={handleOk} onCancel={handleCancel} okText="Simpan" cancelText="Batal">
      {data.map((object, index) => (
        <div className={ObjectWrapper} key={index}>
          <div className={ObjectLine}>
            <Checkbox
              className={CheckboxStyle}
              onChange={e => console.log(e)}
              defaultChecked={true}
            >
              <div className={ObjectDetail}>
                <div className={ObjectName}>{object.name}</div>
              </div>
            </Checkbox>

            <div className={ObjectTotal}>
              <MinusCircleOutlined />
              <Input className={InputTotalObject} defaultValue={object.total} />
              <PlusCircleOutlined />
            </div>
          </div>
        </div>
      ))}
    </Modal>
  );
};

ConfirmModal.propTypes = {
  data: arrayOf(object),
  visible: bool,
  setVisible: func,
};

ConfirmModal.defaultProps = {
  data: [],
  visible: false,
  setVisible: () => {},
};


export default ConfirmModal;