import React, { useRef, useEffect, useState } from 'react';
import * as tf from '@tensorflow/tfjs';
import * as cocossd from "@tensorflow-models/coco-ssd";
import { SyncOutlined, LoadingOutlined } from '@ant-design/icons';
import Webcam from "react-webcam";
import getModel from '../../helpers/getModel';
import ConfirmModal from './ConfirmModal';
// import Draw from './draw';
import { FlipCamera, CollectionStyle, CaptureButton, CaptureContainer } from './style'

const Collection = () => {
  const { loading, returnData } = getModel();
  const [isSelfie, setSelfie] = useState(false);
  const [dataCollected, setDataCollected] = useState([]);
  const [isCaptured, setCaptured] = useState(false);
  const webcamRef = useRef(null);
  const canvasRef = useRef(null);
  const videoConstraints = {
    facingMode: isSelfie ? 'user' : 'environment',
  };

  const detect = async (net) => {
    // Check data is available
    if (
      webcamRef.current &&
      webcamRef.current && net &&
      webcamRef.current.video.readyState === 4
    ) {
      var counts = {};

      // Get Video Properties
      const video = webcamRef.current.video;
      const videoWidth = webcamRef.current.video.videoWidth;
      const videoHeight = webcamRef.current.video.videoHeight;

      // Set video width
      webcamRef.current.video.width = videoWidth;
      webcamRef.current.video.height = videoHeight;

      // Set canvas height and width
      canvasRef.current.width = videoWidth;
      canvasRef.current.height = videoHeight;

      const obj = isCaptured ? dataCollected : await net.detect(video);
      obj.forEach(x => { counts[x.class] = (counts[x.class] || 0) + 1 });

      const mappingObj = Object.keys(counts).map(key => {
        return {
          name: key,
          total: counts[key],
        };
      });

      setDataCollected(mappingObj);

      // Draw mesh
      // const ctx = canvasRef.current.getContext("2d");
      // Draw(obj, ctx)
    }
  };
  
  useEffect(() => {
    let intervalID;

    if (!isCaptured) {
      intervalID = setInterval(() => {
        console.log("Running")
        if (!isCaptured) detect(returnData);
        else clearInterval(intervalID);
      }, 10);
    } else clearInterval(intervalID);

    return () => clearInterval(intervalID);
  }, [isCaptured]);

  const handleCapture = () => {
    setCaptured(true);
  }

  return (
    <div className={CollectionStyle}>
      <div className={FlipCamera}>
        <SyncOutlined onClick={() => setSelfie(false)}/>
      </div>

      <header>
        <Webcam
          ref={webcamRef}
          muted={true}
          videoConstraints={videoConstraints}
          style={{
            position: "absolute",
            marginLeft: "auto",
            marginRight: "auto",
            left: 0,
            right: 0,
            textAlign: "center",
            zindex: 9,
            width: "100%",
            height: "100%",
          }}
        />

        <canvas
          ref={canvasRef}
          style={{
            position: "absolute",
            marginLeft: "auto",
            marginRight: "auto",
            left: 0,
            right: 0,
            textAlign: "center",
            zindex: 8,
            width: "100%",
            height: "100%",
          }}
        />
      </header>

      <ConfirmModal visible={isCaptured} setVisible={setCaptured} data={dataCollected} />
      
      <div className={CaptureContainer}>
        {!loading && (
          <div className={CaptureButton} onClick={handleCapture} />
        )}
        {loading && <LoadingOutlined />}
      </div>
    </div>
  )
}

export default Collection;